<template>
    <div class="box">
        <div class="nav">
            <ul>
                <li v-for="(i,$index) in arr" :key="$index" @click="dian($index,i.c)" :class="index==$index?'active':''">
                    <router-link :to="i.c">
                        <span>{{i.a}}</span>
                        <span>{{i.b}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            arr:[
                {a:'首页',b:'HOME',c:'/',id:0},
                {a:'机械设备',b:'PRODUCTS',c:'/jxsb',id:1},
                {a:'新闻资讯',b:'NEWS',c:'/xwzx',id:2},
                {a:'关于我们',b:'ABOUT',c:'/gywm',id:3},
                {a:'核心团队',b:'TEAM',c:'/hxtd',id:4},
                {a:'联系我们',b:'CONTACT',c:'/lxwm',id:5},
            ],
            index:0,
            moveindex:0,
        }
    },
    methods:{
        dian(index,c){
            if(this.$route.matched[0].path == ''){
                this.$route.matched[0].path = '/'
            }
            if(this.$route.matched[0].path == c){
                this.index = index
            }
        }
    },
    created(){
        for(let i=0;i<this.arr.length;i++){
            if(this.$route.matched[0].path == ''){
                this.$route.matched[0].path = '/'
            }
            if(this.$route.matched[0].path == this.arr[i].c){
                this.index = this.arr[i].id
            }
            
        }
    },
    watch: {
        $route(a){
            a.matched[0].path
            for(let i=0;i<this.arr.length;i++){
                if(this.$route.matched[0].path !== this.arr[i].c){
                    this.index = 999
                }
            }
                
        }
    }
}
</script>

<style scoped>
    .box{
        width: 100%;
        height: 71px;
        background-color: #cf372d;
        user-select: none;
    }
    .nav{
        padding-top: 5px;
        max-width: 1024px;
        margin: 0 auto;
    }
    .nav>ul>li{
        list-style: none;
        float: left;
        margin-right: 5px;
    }
    .nav>ul li:hover{
        background-color: #fff;
    }
    .nav>ul li:hover>a>span{
        color: #cf372d;
    }
    .nav>ul li:hover a>span:nth-of-type(2){
        color: #cf372d;
    }
    .nav>ul>li.active{
        background-color: #fff;
    }
    .nav>ul>li>a{
        text-decoration: none;
    }
    .nav>ul>.active>a>span{
        color: #cf372d;
    }
    .nav>ul>.active>a>span:nth-of-type(2){
        color: #cf372d;
    }
    .nav>ul>li>a>span{
        display: block;
        line-height: 33px;
        padding: 0px 35px;
        color: #fff;
        text-align: center;
        font-size: 15px;
        font-family: '微软雅黑';
    }
    .nav>ul>li>a>span:nth-of-type(2){
        font-size: 12px;
        color: #f2c4c4;
    }
</style>