import Vue from 'vue'
import index from '../view/index.vue'
import jxsb from '../view/jxsb.vue'
import xwzx from '../view/xwzx.vue'
import gywm from '../view/gywm.vue'
import hxtd from '../view/hxtd.vue'
import lxwm from '../view/lxwm.vue'
import text from '../view/text.vue'
import xq from '../view/xq.vue'
import hx from '../view/hx.vue'
import Router from 'vue-router'

Vue.use(Router);

let routes = [
    {path:'/',name:'index',component:index},
    {path:'/jxsb',name:'jxsb',component:jxsb},
    {path:'/xwzx',name:'xwzx',component:xwzx},
    {path:'/gywm',name:'gywm',component:gywm},
    {path:'/hxtd',name:'hxtd',component:hxtd},
    {path:'/lxwm',name:'lxwm',component:lxwm},
    {path:'/text:t',name:'text',component:text},
    {path:'/xq:t',name:'xq',component:xq},
    {path:'/hx',name:'hx',component:hx}
]

export default new Router({
    routes,
    // mode:'history'
})